import React from "react";
import { Route } from "react-router-dom";
import { Auth, Home } from "./pages";

function App() {
  return (
    <div className="App">
      <Route exact path={["/", "/login", "/registration"]} component={Auth} />
      <Route exact path={"/chat"} component={Home} />
    </div>
  );
}

export default App;
